
export class LogManager {
  constructor() {
    this.keepLimit = 30;
    this.logMessages = [];
    this.onUpdateListener = null;
  }

  clear()  {
    this.logMessages = [];
    this.emitUpdate();
  }

  orderAndTrimMessages() {
    this.logMessages.sort((a,b) => {
      return b.date - a.date;
    });

    this.logMessages = this.logMessages.slice(-this.keepLimit);
  }

  getLogMessages() {
    return this.logMessages;
  }

  storeLogMessage(eventDate,type,...data) {
    let messageToStore = {
      date: eventDate,
      type: type,
      data: data
    }

    this.logMessages.push(messageToStore);

    this.orderAndTrimMessages();
    this.emitUpdate();
  }

  log(...data) {
    this.storeLogMessage(new Date(),'LOG',data);
    console.log(data);
  }

  error(...data) {
    this.storeLogMessage(new Date(),'ERROR',data);
    console.error(data);
  }

  debug(...data) {
    this.storeLogMessage(new Date(),'DEBUG',data);
    console.debug(data);
  }

  addUpdateListener(eventListener) {
    this.onUpdateListener = eventListener
  }

  emitUpdate() {
    if (this.onUpdateListener) {
      this.onUpdateListener(this.logMessages);
    }
  }

}
