

export function pushFileDownload(filename,fileContent,mimeType = 'application/octet-stream',isBase64 = false, charset = 'utf-8') {
  let typeString = `${mimeType};charset=${charset}`;
  let fileDataBlobPromise

  if (isBase64) {
    //Base64 content (ussually PDF) needs to be de-base64-ed but without going via a string othewise it alters chars badly
    fileDataBlobPromise = fetch(fileContent)
    .then(res => res.blob());
  } else {
    //do this so we can treat them the same after
    fileDataBlobPromise = Promise.resolve(new Blob([fileContent], {type:typeString}));
  }


  fileDataBlobPromise.then(fileDataBlob => {
    console.log('data',fileDataBlob);
    let filedataUrl = URL.createObjectURL(fileDataBlob);
    let downloadLink = document.createElement("a");
    downloadLink.href = filedataUrl;
    downloadLink.download = filename;
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
    URL.revokeObjectURL(filedataUrl);
  })


  // var link=document.createElement('a');
  // link.href=res; //window.URL.createObjectURL(blob);
  // link.download="myFileName.pdf";
  // link.click();
}

export function requestFileUpload(callback) {
  function handleUploadResult(callback) {
    if (this.files.length > 0) {
      const fileList = this.files; /* now you can work with the file list */
      let fileToRead = this.files[0];

      //Only ever read the first one
      console.log(fileToRead,callback);

      const reader = new FileReader();
      reader.addEventListener('load', (event) => {
        // console.log('File Content',event.target.result);
        callback(event.target.result,fileToRead.name);
      });
      reader.readAsText(fileToRead);
    }
    //Otherwise just don't call the callback

  }

  var uploadLink = document.createElement('input');
  uploadLink.type = 'file';
  uploadLink.style = 'display:none';
  uploadLink.accept = '.js,.json';
  uploadLink.addEventListener("change", handleUploadResult.bind(uploadLink,callback), false);
  document.body.appendChild(uploadLink);
  uploadLink.click();

}
