import { h, Component, render } from 'preact';
import { NavBar, NavBarBlock, NavBarItem, Button, CheckboxSwitch, Icon } from './utilcontrols/utilcontrols.js';
import { GDriveUserButtons } from './etc/gdriveintegration.js';

class ClickToEditInput extends Component {

	constructor(props) {
		super(props);
		this.state = {
			editing: false
		}

		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleUserRequestEdit = this.handleUserRequestEdit.bind(this);
		this.handleExit = this.handleExit.bind(this);
	}
	componentDidMount() {

	}

	handleInputChange(e) {
		this.setState({editing: false});
		if (this.props.onChange) {
			this.props.onChange(e.target.value);
		}

	}

	handleExit() {
		this.setState({editing: false});
	}

	handleUserRequestEdit() {
		console.log('USer requested editing')
		this.setState({editing: true});

		this.handleChangeCurrentFileName
	}

	render(props, state) {
		return (
			h('div',{class: 'click-to-edit-input'},
				(
					state.editing?
					h('input',{
						value: props.value,
						onChange: this.handleInputChange,
						onBlur: this.handleExit,
						class: 'editor'
					},null)
					:
					h('div',{onClick: this.handleUserRequestEdit, class: 'display'},props.value)
				)
			)
		)
	}
}

export class TopNavBar extends Component {
	constructor(props){
		super(props)
		this.state = {
			autoRefreshEnable: false
		}

		this.handleNavCommand = this.handleNavCommand.bind(this);
	}
	componentDidMount() {

	}

	handleInputPanelTabClick(panelName) {
		this.props.onInputPanelChange(panelName);
	}

	handleNavCommand(command,e) {
		console.log(command,e);
		this.props.onNavCommand(command,e);
	}


	render(props, state) {
		let gDriveLeftButtons = null;

		if (props.gDriveSignedIn) {
			gDriveLeftButtons = h(NavBarBlock,null,
				h(NavBarItem,null,
					h('span',null,'Google Drive'),
					h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(null,'gdrive-open'), title: 'Open file from Google Drive'},h(Icon,{icon: 'folder2-open'}))),
					h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(null,'gdrive-save'), title: 'Save file to Google Drive'},h(Icon,{icon: 'cloud-arrow-up'}))),
					h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(null,'gdrive-saveas'), title: 'Save As file to Google Drive'},h(Icon,{icon: 'files'})))
				)
			)
		}
		return (
			h(NavBar,null,
				h(NavBarBlock,{side: 'left'},
					h('div',{class: 'title'},props.appTitle),
					h(NavBarItem,null,
						h(ClickToEditInput,{value: props.currentFileName, onChange: props.onChangeFileName})
					),
					h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(this,'new-file'), title: 'New file'},h(Icon,{icon: 'file-plus'}))),
					h(NavBarBlock,null,
						h('span',null,'Local Device'),
						h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(this,'local-save'), title: 'Save to local device'},h(Icon,{icon: 'download'}))),
						h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(this,'local-open'), title: 'Open local file'},h(Icon,{icon: 'folder-fill'}))),
					),
					gDriveLeftButtons


				),
				h(NavBarBlock,{side: 'right'},
					h(NavBarItem,null,h('a',{onClick: this.handleNavCommand.bind(this,'about')},'About')),
					h(GDriveUserButtons,{
						userSignedIn: props.gDriveSignedIn,
						onUserSignIn: this.handleNavCommand.bind(null,'gdrive-signin'),
						onUserSignOut: this.handleNavCommand.bind(null,'gdrive-signout')
					})
				)
      )
		)
	}
}
