import { h, Component, render } from 'preact';

class OutputLogEntry extends Component {


  render(props,state) {
    let classVal = 'log-entry' + (props.type?(' ' +props.type):'') + (props.level?(' level-'+props.level):'');
    return(
      h('div',{class: classVal},props.data.toString())
    );
  }
}

export class OutputLog extends Component {


	render(props, state) {
    console.log('error props',props.entries);
    let maxEntriesDisplayed = props.max || 10;
    let logEntries = props.entries || [];
    //assume the array is sorted
    logEntries = logEntries.slice(-maxEntriesDisplayed);

		return (
			h('div',{class:'output-log'},logEntries.map(logEntry => h(OutputLogEntry,logEntry)))
		)
	}
}
