import _ from 'underscore';
import { h, Component, render, createRef } from 'preact';
import { NavBar, Button, CheckboxSwitch, Icon } from './utilcontrols/utilcontrols.js';

export class CodeEditor extends Component {
  constructor(props) {
    super(props);
    this.editorRef = createRef();
    //Pass back reference to getValue?
  }


  componentDidMount() {
    let modeString = this.props.mode || 'javascript';
    let themeString = this.props.theme || 'twilight';


    this.codeEditor = ace.edit(this.editorRef.current,{
        highlightActiveLine: true,
        showPrintMargin: false,
        theme: 'ace/theme/'+themeString,
        mode: 'ace/mode/'+modeString,
        tabSize: 2,
    });

    this.onChangeValueDebounced = _.debounce(() => {
      if (this.props.onChangeValue) {
        this.props.onChangeValue(this.codeEditor.getValue());
      }
    },this.props.onChangeValueDebounce || 10000);

    // if (this.props.onChangeValue) {
      this.codeEditor.on("change",(event) => {
        if (this.onChangeValueDebounced) {
          this.onChangeValueDebounced();
        }

      });
      // this.codeEditor.on("change", _.debounce((event) => {
      //   this.props.onChangeValue(this.codeEditor.getValue());
      // },this.props.onChangeValueDebounce || 2000));
    // }

    // if (this.props.onChange) {
    //   this.codeEditor.on("change",(event) => {
    //     this.props.onChange();
    //   });
    //   // this.codeEditor.on("change", _.debounce((event) => {
    //   //   this.props.onChange(this.codeEditor.getValue());
    //   // },this.props.onChangeDebounce || 2000));
    // }

    //Callback allows externals to pull the value from the editor on demand (for file saving etc)
    if (this.props.sessionRegisterCallback) {
      this.props.sessionRegisterCallback(this.props.name,this.codeEditor);
    }

    if (this.props.initValue) {
      this.codeEditor.setValue(this.props.initValue);
    }

	}

	render(props, state) {
    let containerClass = 'editorContainer' + (props.disable?' disable':'') + (props.hidden?' hidden':'');
    return (
        h('div', {class:containerClass, id: props.id},
  				h('div', {class: 'editor', ref: this.editorRef}),
  			)
		);
	}
}
