import { h, Component, render } from 'preact';
import { Icon, NavBarItem, ModalWindow, TextInput, Button} from '../utilcontrols/utilcontrols.js';



export class GDriveFileFolderSelect extends Component {
	constructor(props) {
		super(props);
		this.state = {
			active: false
		}

		this.handleUserClickSelectFolder = this.handleUserClickSelectFolder.bind(this);
		this.handleFileNameChange = this.handleFileNameChange.bind(this);
		this.handleUserClickClose = this.handleUserClickClose.bind(this);
		this.handleUserClickSave = this.handleUserClickSave.bind(this);
	}

	handleUserClickSelectFolder(e) {

		let parent = this;
		let folderPickerCallback = (folder) => {
			console.log('Folder Picked 1',folder);
			parent.setState({currentFolderName: folder.name, currentFolderId: folder.id})
		}
		console.log('REquesting Pcicked',this.props.gDriveFileManager);
		if (this.props.gDriveFileManager) {
			this.props.gDriveFileManager.selectFolderRequest(folderPickerCallback);
		}
	}

	componentDidMount() {
		this.setState({currentFolderName: this.props.currentFolderName, currentFolderId: this.props.currentFolderId});
	}

	openToSelectFolder(currentFileName, successCallback) {
		this.setState({
			active:true,
			successCallback: successCallback
		});
		if (currentFileName) {
			this.setState({currentFileName: currentFileName});
		}
	}

	openToSelectFolderAndFile() {

	}

	handleFileNameChange(e) {
		this.setState({currentFileName: e.target.value});
	}

	handleUserClickClose() {
		this.setState({active: false})
	}

	handleUserClickSave() {
		console.log('User Clicked save');
		this.setState({active:false});
		if (this.state.successCallback) {
			this.state.successCallback(this.state.currentFolderId,this.state.currentFileName);
		}
	}

	render(props, state) {
		let currentFileNameValue = state.currentFileName
		let footerControls = [
			h(Button,{onClick: this.handleUserClickSave, value: 'Save'}),
			h(Button,{onClick: this.handleUserClickClose, value: 'Cancel', class: 'secondary'})
		]
		return (
			h(ModalWindow,{
				active: state.active,
				onClose: this.handleUserClickClose,
				title: 'Save as',
				footer: footerControls
			},
				h('div',{class:'input-form'},
					h('div',{class: 'section'},
						h(TextInput,{label: 'Name',value: currentFileNameValue, onChange: this.handleFileNameChange}),
						h('div',{class: 'field'},
							h('label',null,'Folder'),
							h('div',{class: 'readonly-field', onClick: this.handleUserClickSelectFolder },(state.currentFolderName?state.currentFolderName:'Click to select folder')),
						)
					)
				)
			)
		)
	}
}



export class GDriveUserButtons extends Component {
	constructor(props) {
		super(props);

	}

	componentDidMount() {

	}

	render(props, state) {
		let renderData = null;

		if (props.userSignedIn) {
			renderData = (
				h(NavBarItem,null,
					h('imv',{src: props.avatarUrl}),
					h(NavBarItem,null,h('a',null,props.userFullname)),
					h(NavBarItem,null,h('a',{onClick: props.onUserSignOut},'Sign Out'))
				)
			)
		} else {
			renderData = (
				h(NavBarItem,null,
					h(NavBarItem,null,h('a',{onClick: props.onUserSignIn},'Sign in to Google Drive'))
				)
			)
		}

		return renderData;
	}

}

export class GDriveFileManager {

	constructor(options) {
		this.pickerApiLoaded = false;
		this.authenticated = false;
		this.appId = options.appId;
		this.apiKey = options.apiKey;
		this.clientId = options.clientId;
		this.clientInitialized = false;
		this.currentFile = null;
	  this.gapiSettings = {
			scope: 'https://www.googleapis.com/auth/drive.file',
			discoveryDocs: ["https://www.googleapis.com/discovery/v1/apis/drive/v3/rest"]
		}

		this.onGapiClientLoad = this.onGapiClientLoad.bind(this);
		this.onPickerApiLoad = this.onPickerApiLoad.bind(this);
		this.eventHandlers = {};



	}

	onStatusUpdateHandler(handler) {
		this.eventHandlers.statusUpdate = handler;
	}

	getOAuthToken() {
		if (gapi.auth2.getAuthInstance().isSignedIn.get()){
			return window.gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().access_token;
		}
	}

	updateSigninStatus(isSignedIn) {
		console.log('Updating Sign In Stats',isSignedIn);
		if (isSignedIn) {
			this.authenticated = true;
		} else {
			this.authenticated = false;
		}

		if (this.eventHandlers.statusUpdate) {
			this.eventHandlers.statusUpdate(isSignedIn);
		}
	}

	init() {

		const script = document.createElement("script");
	  script.src = "https://apis.google.com/js/api.js";
	  script.async = true;
	  script.onload = () => this.loadGapi();

	  document.body.appendChild(script);
		//<script async defer src="https://apis.google.com/js/api.js"
		//   onload="this.onload=function(){};handleGoogleAPIClientLoad()"
		//   onreadystatechange="if (this.readyState === 'complete') this.onload()">
		// </script>
		// this.loadGapi();
	}

	loadGapi() {
		window.gapi.load('client:auth2:picker', this.onGapiClientLoad);
		// window.gapi.load('picker', {'callback': this.onPickerApiLoad});
	}

	onGapiClientLoad() {
		console.log('Client Init Start');
		let parent = this;
		window.gapi.client.init({
			apiKey: this.apiKey,
			clientId: this.clientId,
			discoveryDocs: this.gapiSettings.discoveryDocs,
			scope: this.gapiSettings.scope
		}).then((f) => {
			// Listen for sign-in state changes.
			console.log('Updating parent',parent);
			let p = parent.updateSigninStatus.bind(parent);
			window.gapi.auth2.getAuthInstance().isSignedIn.listen(p);
			// console.log(parent.getOAuthToken());
			parent.updateSigninStatus(gapi.auth2.getAuthInstance().isSignedIn.get());
			parent.clientInitialized = true;
			// parent.userSignIn();
			this.onPickerApiLoad();
			console.log('Client Init Finalized',f,gapi.auth2);
		}, function(error) {
			console.log(error);
		}).catch((f) =>{
			console.log(f);
		});
	}

	onPickerApiLoad() {
		this.pickerApiLoaded = true;
		console.log('PIcker loaded');
	}

	refreshAuthToken(){
		window.gapi.auth2.getAuthInstance().currentUser.get().reloadAuthResponse();
	}

	userSignIn() {
		window.gapi.auth2.getAuthInstance().signIn();
	}

	userSignOut() {
		window.gapi.auth2.getAuthInstance().signOut();
	}

	createFilePicker(pickerCallback) {
		if (this.pickerApiLoaded && gapi.auth2.getAuthInstance().isSignedIn.get()) {
			// var view = new google.picker.View(google.picker.ViewId.DOCS);
			// view.setMimeTypes("image/png,image/jpeg,image/jpg");
			var picker = new google.picker.PickerBuilder()
					// .enableFeature(google.picker.Feature.NAV_HIDDEN)
					// .enableFeature(google.picker.Feature.MULTISELECT_ENABLED)
					.setAppId(this.appId)
					// .setOrigin(origin)
					.setOAuthToken(this.getOAuthToken())
					// .addView(view)
					// .addView(new google.picker.DocsUploadView())
					.addView(new google.picker.DocsView().setIncludeFolders(true).setMode(google.picker.DocsViewMode.LIST).setMimeTypes('text/javascript'))
					.setDeveloperKey(this.apiKey)
					.setCallback(pickerCallback)
					.build();
			 picker.setVisible(true);
		}
	}

	createFolderPicker(pickerCallback) {
		if (this.pickerApiLoaded && gapi.auth2.getAuthInstance().isSignedIn.get()) {
			// var view = new google.picker.View(google.picker.ViewId.DOCS);
			// view.setMimeTypes("image/png,image/jpeg,image/jpg");
			var picker = new google.picker.PickerBuilder()
					// .enableFeature(google.picker.Feature.NAV_HIDDEN)
					// .enableFeature(google.picker.Feature.MULTISELECT_ENABLED)
					.setAppId(this.appId)
					// .setOrigin(origin)
					.setOAuthToken(this.getOAuthToken())
					// .addView(view)
					// .addView(new google.picker.DocsUploadView())
					.addView(new google.picker.DocsView().setIncludeFolders(true).setMode(google.picker.DocsViewMode.LIST).setIncludeFolders(true).setMimeTypes('application/vnd.google-apps.folder').setSelectFolderEnabled(true))
					.setDeveloperKey(this.apiKey)
					.setCallback(pickerCallback)
					.build();
			 picker.setVisible(true);
		}
	}

	ensureUserSignedIn(){
		if (gapi.auth2.getAuthInstance().isSignedIn.get()) {
			return Promise.resolve(true)
		} else {
			return window.gapi.auth2.getAuthInstance().signIn();
		}

	}

	selectFolderRequest(folderSelectCallback) {
		let parent = this;
		this.ensureUserSignedIn().then((f) => {
			let pickerCallback = function(data) {
				if (data.action == google.picker.Action.PICKED) {
					var fileId = data.docs[0].id;
					console.log ('User Selected File');
					console.log(data.docs[0]);
					// alert('The user selected: ' + fileId);
					folderSelectCallback(data.docs[0])
				 }
			}

			this.createFolderPicker(pickerCallback);
		}).catch((e) => {
			console.log('Error sign in',e);
		});
	}

	openFileRequest(fileOpenCallback) {
		let parent = this;
		this.ensureUserSignedIn().then((f) => {
			console.log('THE PROMISE IS FULLFLLLED',f,gapi.auth2.getAuthInstance().isSignedIn.get())
			let pickerCallback = function(data) {
				if (data.action == google.picker.Action.PICKED) {
					var fileId = data.docs[0].id;
					console.log ('User Selected File');
					console.log(data.docs);
					// alert('The user selected: ' + fileId);
					parent.fileMeta = data.docs[0];
					parent.driveDownloadFile(data.docs[0],fileOpenCallback)
				 }
			}

			this.createFilePicker(pickerCallback);
		}).catch((e) => {
			console.log('Error sign in',e);
		});
	}

	driveDownloadFile(fileMeta, fileOpenCallback){
		window.gapi.client.drive.files.get({
				fileId:fileMeta.id,
				alt: 'media'
		}).then(function(resp){
			console.log(resp);
			fileOpenCallback(fileMeta,resp.body);
		});
	}

	driveUpdateFile(fileMeta, content) {
		const donePromise = new Promise((resolve, reject) => {

			var request = gapi.client.request({
				'path': '/upload/drive/v3/files/' + fileMeta.id,
				'method': 'PATCH',
				'params': {'uploadType': 'media', 'alt': 'json'},
				'headers': {
					'Content-Type': 'text/javascript',
					'Content-Length': content.length
				},
				//Other properies go here
				'body': content
			}).then((response) => {
					resolve(response);
				},(error) => {
					reject(error);
				});
		});


		return donePromise;
	}

	driveCreateFile(fileMeta, data) {
		const donePromise = new Promise((resolve, reject) => {


		const boundary = '-------314159265358979323846';
	  const delimiter = "\r\n--" + boundary + "\r\n";
	  const close_delim = "\r\n--" + boundary + "--";

	  const contentType = 'text/javascript';

	  var metadata = Object.assign({
	      'mimeType': contentType
	    },fileMeta);

	    var multipartRequestBody =
	        delimiter +
	        'Content-Type: application/json\r\n\r\n' +
	        JSON.stringify(metadata) +
	        delimiter +
	        'Content-Type: ' + contentType + '\r\n\r\n' +
	        data +
	        close_delim;

	    var request = gapi.client.request({
	        'path': '/upload/drive/v3/files',
	        'method': 'POST',
	        'params': {'uploadType': 'multipart'},
	        'headers': {
	          'Content-Type': 'multipart/related; boundary="' + boundary + '"'
	        },
	        'body': multipartRequestBody}).then((response) => {
						console.log('ff',response);
					resolve(response);
				},(error) => {
					reject(error);
				});
		});


		return donePromise;
	}

	saveFileRequest(fileMeta, content){
		console.log(fileMeta,content);
		if ((fileMeta) && (fileMeta.id)) {
			console.log('Saving')
			return this.driveUpdateFile(fileMeta,content);
		} else {

			return this.saveAsFileRequest(fileMeta,content)
		}

		// return Promise.resolve(true);
	}

	saveAsFileRequest(fileMeta, content) {
		console.log('savas');
		return this.driveCreateFile(fileMeta,content);
	}
}
