import ace from 'ace-builds/src-noconflict/ace';
import RenderManager from './rendermanager.js';
import Split from 'split.js';
import { h, Component, render, Fragment, createRef } from 'preact';
import { OutputBlock } from './outputblock.js';
import { TopNavBar } from './topnavbar.js';
import { TabGroup, Tab } from './utilcontrols/utilcontrols.js';
import { LogManager } from './logmanager.js';
import newFileTemplate from './etc/newfiletemplate.js';
import { pushFileDownload, requestFileUpload} from './utilcontrols/localfilehandling.js';
import { AboutModal } from './etc/aboutmodal.js';
import { AutoSaveManager } from './utilcontrols/autosavemanager.js';
import _ from 'underscore';
import { GDriveFileManager, GDriveFileFolderSelect} from './etc/gdriveintegration.js';
import { InputBlock } from './utilcontrols/inputblock.js';

ace.config.set('basePath', 'ace-builds');

class App extends Component {
  constructor(props) {
    super(props);
    // Don't call this.setState() here!
    this.renderManager = new RenderManager({
      renderWorker: new Worker(new URL('./renderWorker.js', import.meta.url),{type:'module'}),
    });

    this.autoSaveManager = new AutoSaveManager();

    this.gDriveFileManager = new GDriveFileManager({
      apiKey: process.env.GOOGLE_API_KEY,
      appId : process.env.GOOGLE_APP_ID,
      clientId: process.env.GOOGLE_CLIENT_ID
    });


    window.gDriveFileManager = this.gDriveFileManager;
    gDriveFileManager.init();

    this.newTemplate = newFileTemplate;
    this.logManager = new LogManager;



    this.state = {
      userCode: '',
      autoPreview: false,
      autoPreviewDelay: 3000,
      autoSave: true,
      autoSaveDelay: 3000,
      renderPreviewOutput: '',
      renderPreviewFormat: 'SVG',
      renderConsole: [],
      logMessages: [],
      showAboutForm: false,
      currentFileName: 'test',
      gDriveFileMeta: null,
      gDriveSignedIn: false,
    }
    this.inputBlockRef = createRef();
    this.gDriveFileFolderSelectRef = createRef();

    //Bind the event handlers
    this.handleUserCodeChange = this.handleUserCodeChange.bind(this);
    this.handleRequestRender = this.handleRequestRender.bind(this);
    this.handleChangeAutoPreview = this.handleChangeAutoPreview.bind(this);
    this.handleRenderDownloadRequest = this.handleRenderDownloadRequest.bind(this);
    this.handleCodeLoadRequest = this.handleCodeLoadRequest.bind(this);
    this.handleCodeSaveRequest = this.handleCodeSaveRequest.bind(this);

    this.pushUserCodeToEditor = this.pushUserCodeToEditor.bind(this);
    this.handleNewFileREquest = this.handleNewFileREquest.bind(this);

    this.handleLogUpdate = this.handleLogUpdate.bind(this);
    this.logManager.addUpdateListener(this.handleLogUpdate);

    this.handleAboutClose = this.handleAboutClose.bind(this);
    this.handleAboutShow = this.handleAboutShow.bind(this);
    this.handleNavCommand = this.handleNavCommand.bind(this);
    this.handleChangeCurrentFileName = this.handleChangeCurrentFileName.bind(this);

    this.handleGDriveFileOpen = this.handleGDriveFileOpen.bind(this);
    this.handleGDriveFileSave = this.handleGDriveFileSave.bind(this);
    this.handleGDriveFileSaveAs = this.handleGDriveFileSaveAs.bind(this);

    this.handleGDriveSignInStatusChange = this.handleGDriveSignInStatusChange.bind(this);
    this.handleGDriveSignInRequest = this.handleGDriveSignInRequest.bind(this);
    this.handleGDriveSignOutRequest = this.handleGDriveSignOutRequest.bind(this);

    gDriveFileManager.onStatusUpdateHandler(this.handleGDriveSignInStatusChange);
  }

  autoSaveUserCode() {
    console.log('Auto save started');
    this.autoSaveManager.store(this.pullUserCodeFromEditor(InputBlock.INPUT_PANELS.CODE));
  }

  handleUserCodeChange(e) {
    //Don't run update if there is no change
    if (e != this.state.userCode) {
      this.setState({
        userCode: e
      });

      this.logManager.clear();

      this.renderManager.drawCode = e;
      if (this.state.autoPreview) {
        //Assume the delay was pushed down to the editor itself
        this.renderManager.requestRender()
      }



      if (this.state.autoSave) {
        this.autoSaveUserCodeDebounced();
      }
    }

  }

  handleGDriveSignInStatusChange(newSignInStatus) {
    this.setState({gDriveSignedIn : newSignInStatus});
  }

  handleRequestRender(format,returnAction) {
    this.logManager.clear();
    //TODO: update the code from state
    this.renderManager.drawCode = this.pullUserCodeFromEditor(InputBlock.INPUT_PANELS.CODE);
    try {
      let userParamsString = this.pullUserCodeFromEditor(InputBlock.INPUT_PANELS.USER_PARAMS)
      if ((userParamsString) && (userParamsString != '')) {
        this.renderManager.userParams = JSON.parse(userParamsString);
      }
    } catch (e) {
      this.logManager.error('Error Processing User Params',e);
    }

    //TODO: also pull the render params (don't assume they were committed);
    this.renderManager.requestRender(format,returnAction)
  }

  handleChangeAutoPreview(setAutoPreview) {
    this.setState({autoPreview : setAutoPreview})
  }

  handleLogUpdate(messages) {
    this.setState({
      logMessages : messages
    });
  }

  handleNavCommand(command) {
    console.log("Command request",command);
    switch(command) {
      case 'about': this.handleAboutShow(); break;
      case 'gdrive-open': this.handleGDriveFileOpen(); break;
      case 'gdrive-save': this.handleGDriveFileSave(); break;
      case 'gdrive-saveas': this.handleGDriveFileSaveAs();break;
      case 'gdrive-signin': this.handleGDriveSignInRequest();break;
      case 'gdrive-signout': this.handleGDriveSignOutRequest();break;
      case 'local-save': this.handleCodeSaveRequest(); break;
      case 'local-open': this.handleCodeLoadRequest(); break;
      case 'new-file': this.handleNewFileREquest(); break;
    }
  }


  handleGDriveFileOpen() {
    let fileReceiver = (fileMeta, fileContent) => {
      console.log(fileMeta, fileContent);
      this.setState({gDriveFileMeta: fileMeta});
      this.setCurrentFileName(fileMeta.name);
      this.pushUserCodeToEditor(fileContent);
    }
    this.gDriveFileManager.openFileRequest(fileReceiver);
  }

  disconnectGDriveFileMeta(currentFilename = null) {
    this.setState({gDriveFileMeta: null});
  }

  handleGDriveFileSaveResult(resultData) {
    let fileMeta = resultData.result;
    this.setState({gDriveFileMeta: fileMeta});
    this.setCurrentFileName(fileMeta.name);
  }

  handleGDriveFileSave() {
    //Don't try and save files that aren't from GDrive
    if (this.state.gDriveFileMeta) {
      let updateResponse = (f) => {
        console.log(f);
      }
      let userCode = this.pullUserCodeFromEditor(InputBlock.INPUT_PANELS.CODE);

      // this.gDriveFileMeta.name = this.currentFileName;
      let gDriveMeta = this.state.gDriveFileMeta;

      this.gDriveFileManager.saveFileRequest(gDriveMeta,userCode).then(this.handleGDriveFileSaveResult.bind(this)).catch((e) => console.log('ERROR update',e))
    } else {
      this.handleGDriveFileSaveAs();
    }

  }

  handleGDriveFileSaveAs() {

    let folderSelectCallback = (folderId, fileName) => {
      console.log('Selected',folderId,fileName);
      let userCode = this.pullUserCodeFromEditor(InputBlock.INPUT_PANELS.CODE);

      this.gDriveFileManager.saveAsFileRequest({
        'name': fileName,
        parents: [folderId]
      },userCode).then(this.handleGDriveFileSaveResult.bind(this)).catch((e) => {
        console.log('Save Error',e);
      })
    };

    folderSelectCallback = folderSelectCallback.bind(this);
    this.gDriveFileFolderSelectRef.current.openToSelectFolder(this.state.currentFileName,folderSelectCallback);
  }

  handleGDriveSignInRequest() {
    this.gDriveFileManager.userSignIn();
  }

  handleGDriveSignOutRequest(){
    this.gDriveFileManager.userSignOut();
  }

  changeActiveInputPanel(newPanel) {
    //TODO: Fix this handle this switch properly
    console.log('Input panel change',newPanel);
    //preserve whatever is on the current panel - except if its the code panel as that is only ever hidden
    if (this.state.activeInputPanel == InputBlock.INPUT_PANELS.OPTIONS) {
      //Store the options
      let setOptions = this.editorSessions[InputBlock.INPUT_PANELS.OPTIONS].getValue();
      try {
        this.loadRenderOptionsFromJSON(setOptions);
      } catch (e) {
        this.logManager.error('Error processing JSON from Options',e);
      }
      //force the resulting options back into the codeEditor so it only shows valid code
      //TODO FIx this - for some reason this only works after the user clicks into the editor?
      // this.editorSessions[INPUT_PANELS.OPTIONS].setValue(this.renderOptionsToJSON());

    }

    if (this.state.activeInputPanel == InputBlock.INPUT_PANELS.USER_PARAMS) {
      //Store the user params
      let setParams = this.editorSessions[InputBlock.INPUT_PANELS.USER_PARAMS].getValue();

      try {
        this.loadUserParamsFromJSON(setParams);
      } catch (e) {
        this.logManager.error('Error processing JSON from User Params',e);
      }
    }

    this.setState({ activeInputPanel: newPanel })

  }

  handleRenderDownloadRequest(format) {
    console.log('Render Download Requested');
  }

  getInputBlock() {
    return this.inputBlockRef.current;
  }

  pushUserCodeToEditor(codeToLoad) {
    this.getInputBlock().setContent(codeToLoad,'single',InputBlock.INPUT_PANELS.CODE);
  }

  pullUserCodeFromEditor(codePanel = InputBlock.INPUT_PANELS.CODE){
    return this.getInputBlock().getContent('single',codePanel);
  }

  handleCodeLoadRequest() {
    console.log('Code load requested');
    let fileResultHandler = function(content,fileName) {
      this.setCurrentFileName(fileName);
      this.pushUserCodeToEditor(content);
      //reset the google drive setup so we don't mix the two
      this.setState({gDriveFileMeta: null});
      this.disconnectGDriveFileMeta();
    }
    requestFileUpload(fileResultHandler.bind(this));
  }


  handleCodeSaveRequest(saveAs = false) {
    console.log('Code Save Requested');
    let userCode = this.pullUserCodeFromEditor(InputBlock. INPUT_PANELS.CODE);

    pushFileDownload((this.state.currentFileName?(this.state.currentFileName+'.js'):'usercode.js'),userCode,'application/json');
  }

  handleNewFileREquest() {
    console.log('New File Requested');
    this.setCurrentFileName('New File');
    this.pushUserCodeToEditor(this.newTemplate)
    this.disconnectGDriveFileMeta();
  }

  renderOptionsToJSON() {
    console.log('Request render options in JSON');
    return this.renderManager.renderOptionsToJSON();
  }

  getRenderDefaultOptionsJSON(){
    return this.renderManager.renderOptionsToJSON();
  }

  loadRenderOptionsFromJSON(newOptions) {
    this.renderManager.loadRenderOptionsFromJSON(newOptions);
  }

  handleAboutClose() {
    console.log('showAboutFormCose');
    this.setState({
      showAboutForm: false
    })
  }

  handleAboutShow() {
    this.setState({
      showAboutForm: true
    })
  }

  userParamsToJSON() {
    console.log('Request USer PArams for display');
    return '';

  }

  loadUserParamsFromJSON(newOptions) {
    // this.renderManager.loadRenderOptionsFromJSON(newOptions);
    console.log('USer Params load requested');
  }

  setCurrentFileName(newFileName) {
    this.setState({currentFileName: newFileName.replace(/\.[^/.]+$/, "")});
  }

  handleChangeCurrentFileName(newFileName) {
    this.setCurrentFileName(newFileName);
  }

	componentDidMount() {

    this.renderManager.on('renderStart',()=>{console.log('Render Starting')});
    this.renderManager.on('renderOutput',(data)=>{
      console.log('Render Output Received',data);

      if ((data.returnAction) && (data.returnAction == 'download')) {
        //extract just the data from the data URL;
        console.log(data.format,(data.format == 'PDF'))
        pushFileDownload(
          `RenderResult.${data.format}`,
          data.output,
          'application/' + (data.format=='PDF'?'PDF':'svg+xml'),
          (data.format == 'PDF')
        );

      } else {
        //Assume preview
        let renderPreviewFormat = data.format;
        let renderPreviewOutput = data.output;
        this.setState({
          renderPreviewFormat,
          renderPreviewOutput
        });
      }



    });
    this.renderManager.on('renderLog',(data)=>{console.log('Render Log',data.message)});
    this.renderManager.on('renderError',(data)=>{
      console.log('Render Error',data);
      this.logManager.error(data.message,`line: ${data.lineno}`);
    });
    this.renderManager.on('renderFinish',()=>{console.log('Render Complete')});


    Split(['#inputContainer','#outputContainer'], {
      gutterSize: 10,
      minSize: 100,
      onDragEnd: () => {
        //Update any loaded code editor sessions
        this.getInputBlock().sizeRefresh();
      }
    });

    Split(['#renderOutputContainer','#errorsOutputContainer'], {
      sizes: [80,20],
      gutterSize: 10,
      minSize: [200,5],
      direction: 'vertical',
    });

    //debounce usercodechange function

    //Fetch any autosaved content
    //Setup auto save
    console.log('Setting auto save to:',this.state.autoSaveDelay);
    this.autoSaveUserCodeDebounced = _.debounce(this.autoSaveUserCode,this.state.autoSaveDelay || 15000);
    this.autoSaveManager.retrieve().then((autoSaveResponse) => {
      console.log('Auto save response',autoSaveResponse);
      if (autoSaveResponse) {
        // this.setState({userCode: autoSaveResponse});
        this.pushUserCodeToEditor(autoSaveResponse);
      }
    }).catch((e) => {
      console.log('Auto save response error',e);
    })

	}
	render(props, state) {
    // f = this.handleUserCodeChangeDebounced;
		return (
      h(Fragment,null,
        h(TopNavBar,{
          appTitle: 'LPT',
          onNavCommand: this.handleNavCommand,
          currentFileName: state.currentFileName,
          onChangeFileName: this.handleChangeCurrentFileName,
          gDriveSignedIn: state.gDriveSignedIn
        }),
  			h('div', {id:'appPrimary'},
          h(InputBlock,{
            onChangeDebounce: 2000,
            onEditorValueChange: this.handleUserCodeChange,
            ref: this.inputBlockRef,
            initCode: this.newTemplate,
            initOptions: this.getRenderDefaultOptionsJSON(),
            initUserParams: ''
          },null),
          h(OutputBlock,{
            previewFormat: state.renderPreviewFormat,
            previewOutput: state.renderPreviewOutput,
            onRequestRender: this.handleRequestRender,
            autoPreview: state.autoPreview,
            onAutoPreviewChange: this.handleChangeAutoPreview,
            logMessages: state.logMessages,
          })
        ),
        h(AboutModal,{onClose: this.handleAboutClose, active: state.showAboutForm}),
        h(GDriveFileFolderSelect,{
          gDriveFileManager: this.gDriveFileManager,
          ref: this.gDriveFileFolderSelectRef
        })
      )
		);
	}
}



const app = h(App, null, null);


render(app, document.getElementById('appContainer'));
