import { h, Component, render } from 'preact';
import { PreviewRenderer } from './previewrenderer.js';
import { NavBar, NavBarItem, Button, Icon, CheckboxSwitch } from './utilcontrols/utilcontrols.js';
import { OutputLog } from './outputlog.js';

class RenderNavBar extends Component {
	constructor(props) {
		super(props);
		this.handleRenderRefreshClick = this.handleRenderRefreshClick.bind(this);
		this.handleAutoPreviewClick = this.handleAutoPreviewClick.bind(this);
		this.handleDownloadClick = this.handleDownloadClick.bind(this);
	}

	handleRenderRefreshClick(e) {
		this.props.onRequestRender();
	}

	handleAutoPreviewClick(){
		this.props.onAutoPreviewChange(!this.props.autoPreview);
	}

	handleDownloadClick(downloadFormat){
		this.props.onRequestRender(downloadFormat,'download');
	}

	render(props, state) {
		return (
			h(NavBar,null,
				h(NavBarItem,null,
					h('a',{ onClick: this.handleRenderRefreshClick },
				  	h(Icon,{icon: 'arrow-repeat'})
					)
				),
				h(NavBarItem,{active: props.autoPreview},h('a',{onClick: this.handleAutoPreviewClick},'Auto')),
				h(NavBarItem,null,h('a',{onClick: this.handleDownloadClick.bind(this,'PDF')},'Download PDF')),
				h(NavBarItem,null,h('a',{onClick: this.handleDownloadClick.bind(this,'SVG')},'Download SVG')),
			)
		)
	}
}

export class OutputBlock extends Component {
	componentDidMount() {

	}
	render(props, state) {
		return (
			h('div', {id:'outputContainer'},
				h(RenderNavBar,{
					onRequestRender: props.onRequestRender,
					autoPreview: props.autoPreview,
					onAutoPreviewChange: props.onAutoPreviewChange
				}),
				h('div', {id:'renderOutputContainer'},
					h(PreviewRenderer, {
						previewFormat: props.previewFormat,
						previewOutput: props.previewOutput
					},'')
				),
        h('div', {id:'errorsOutputContainer'},
					h(OutputLog,{max: 15, entries: props.logMessages})
				),


      )
		);
	}
}
