export class AutoSaveManager {
  constructor() {
    //Try to setup cache save -
    this.cache = this.getCache();
  }

  getCache() {
    return caches.open('lpt');
  }

  store(userCode,name = 'auto.js') {
    this.getCache().then(cache => {
      cache.put('/'+name, new Response(userCode));
    });
  }

  retrieve(name = 'auto.js') {
    return this.getCache().then(cache => {
      return cache.match('/'+name).then((cacheResponse) => {
        if (cacheResponse) {
          return cacheResponse.text();
        } else {
          return Promise.resolve('');
        }
      });
    });
  }

  exists(name = 'auto') {

  }
}
