import { h, Component, render } from 'preact';
import { NavBar, NavBarItem, Button, Icon, CheckboxSwitch } from './utilcontrols.js';
import { CodeEditor } from '../codeeditor.js';

export class InputBlock extends Component {
  static  INPUT_PANELS = {
    CODE: 'CODE',
    OPTIONS: 'OPTIONS',
    USER_PARAMS: 'USER_PARAMS'
  };

  constructor(props) {
    super(props);

    this.editorSessions = {};

    this.state = {
      activeInputPanel: InputBlock.INPUT_PANELS.CODE
    }

    this.handleInputPanelTabClick = this.handleInputPanelTabClick.bind(this);
    this.handleSessionRegisterCallback = this.handleSessionRegisterCallback.bind(this);
    this.handleEditorValueChange = this.handleEditorValueChange.bind(this);
  }

	componentDidMount() {

	}

  handleInputPanelTabClick(panelSelect) {
    this.setState({activeInputPanel: panelSelect});
  }

  handleSessionRegisterCallback(editorName,editorSession) {
    this.editorSessions[editorName] = editorSession
  }


  setContent(content, format = 'combined', inputPanel = null) {
    if (format == 'single') {
      if (this.editorSessions[inputPanel]) {
        this.editorSessions[inputPanel].setValue(content);
      }
    } else if (format == 'combined' ) {
      if (content.userCode) {
        this.setContent(content.code, format='single', inputPanel = InputBlock.INPUT_PANELS.CODE);
      }

      if (content.options) {
        this.setContent(content.options, format='single', inputPanel = InputBlock.INPUT_PANELS.OPTIONS);
      }

      if (content.userParams) {
        this.setContent(content.userParams, format='single', inputPanel = InputBlock.INPUT_PANELS.USER_PARAMS);
      }
    }
  }

  getContent(format = 'combined', inputPanel = null) {
    if ((format == 'single') && (inputPanel != null)) {
      if (this.editorSessions[inputPanel]) {
        return this.editorSessions[inputPanel].getValue();
      } else {
        return null;
      }
    } else if (format == 'combined') {
      result = {};
      if ((inputPanel == null) || (inputPanel == InputBlock.INPUT_PANELS.CODE)) {
        result.code = this.getContent('single',InputBlock.INPUT_PANELS.CODE);
      }

      if ((inputPanel == null) || (inputPanel == InputBlock.INPUT_PANELS.OPTIONS)) {
        result.options = this.getContent('single',InputBlock.INPUT_PANELS.OPTIONS);
      }

      if ((inputPanel == null) || (inputPanel == InputBlock.INPUT_PANELS.USER_PARAMS)) {
        result.userParams = this.getContent('single',InputBlock.INPUT_PANELS.USER_PARAMS);
      }
    }
  }

  setPanelVisible(inputPanel) {
    if (inputPanel) {
      this.setState({activeInputPanel: inputPanel});
    }
  }

  handleEditorValueChange(e) {
    if (this.props.onEditorValueChange) {
      this.props.onEditorValueChange(e,InputBlock.INPUT_PANELS.code);
    }
  }

  sizeRefresh(){
    Object.values(this.editorSessions).forEach(session => {
      if (session) {
        session.resize();
      }
    });
  }

	render(props, state) {
		return (
      h('div', {id:'inputContainer'},
        h(NavBar,null,
          h(NavBarItem,null,
            h('a',{className: 'tab', onClick: this.handleInputPanelTabClick.bind(this,'CODE'), active: (state.activeInputPanel==InputBlock.INPUT_PANELS.CODE)},'Model')
          ),
          h(NavBarItem,null,
            h('a',{onClick: this.handleInputPanelTabClick.bind(this,'OPTIONS'), active: (state.activeInputPanel==InputBlock.INPUT_PANELS.OPTIONS)},'Options')
          ),
          h(NavBarItem,null,
            h('a',{onClick: this.handleInputPanelTabClick.bind(this,'USER_PARAMS'), active: (state.activeInputPanel==InputBlock.INPUT_PANELS.USER_PARAMS)},'User Params')
          ),
        ),
        h(CodeEditor,{
          onChangeValue: this.handleEditorValueChange,
          onChangeValueDebounce: props.onChangeDebounce,
          initValue: props.initCode,
          sessionRegisterCallback: this.handleSessionRegisterCallback,
          name: InputBlock.INPUT_PANELS.CODE,
          id: 'userCodeEditor',
          hidden: (state.activeInputPanel!=InputBlock.INPUT_PANELS.CODE)
        }),
        //TODO: find a way to change this to conditional rendering and ensure the reset value is set properly
        h(CodeEditor,{
          initValue: props.initOptions,
          sessionRegisterCallback: this.handleSessionRegisterCallback,
          name: InputBlock.INPUT_PANELS.OPTIONS,
          mode: 'json',
          id: 'optionsCodeEditor',
          hidden: (state.activeInputPanel!=InputBlock.INPUT_PANELS.OPTIONS)
        }),
        h(CodeEditor,{
          initValue: props.initUserParams,
          sessionRegisterCallback: this.handleSessionRegisterCallback,
          name: InputBlock.INPUT_PANELS.USER_PARAMS,
          mode: 'json',
          id: 'userParamsCodeEditor',
          hidden: (state.activeInputPanel!=InputBlock.INPUT_PANELS.USER_PARAMS)
        })
      )
    )
	}
}
