import { h, Component, render } from 'preact';

export class NavBar extends Component {
	componentDidMount() {

	}

	render(props, state) {
		let classPropVal = 'navbar ' + (props.orientation || 'horizontal');
		return (
			h('div',{
				class: classPropVal},
				props.children
			)
		)
	}
}

export class NavBarBlock extends Component {
	componentDidMount() {

	}

	render(props, state) {
		let positionStyle = ' ' + (props.side == 'right'?'right':'left')
		return (
			h('div',{ class: 'navbar-block ' + positionStyle },
				props.children
			)
		)
	}
}


export class NavBarItem extends Component {
	componentDidMount() {

	}

	render(props, state) {
		return (
			h('div',{ class: 'navbar-item' + (props.active?' active':'') },
				props.children
			)
		)
	}
}





export class Icon extends Component {
	componentDidMount() {

	}

	render(props, state) {
		return (
			h('i',{
				class: 'bi bi-'+props.icon
				},
				props.children
			)
		)
	}
}

export class SideBar extends Component {
	componentDidMount() {

	}

	render(props, state) {
		let classPropVal = 'sidebar ' + (props.orientation || 'left') + ' ' + (props.visible?'visible':'');
		return (
			h('div',{
				class: classPropVal,
				id: props.id,
			},props.children)
		)
	}
}

export class CheckboxSwitch extends Component {
	constructor(props) {
		super(props)
	}
	componentDidMount() {
	}

	render(props, state) {
		return (
			h('div',{class: 'switch-slider'},
				h('input', {
	        type: 'checkbox',
					id: props.id,
	        checked: props.checked,
	        onChange: props.onChange
	      }),
				h('label',{for: props.id},
					h('span',{ class: 'slider'}),
				  h('span',{class: 'label'},props.label,props.children)
				)
			)
		)
	}
}

export class NavLink extends Component {
	render(props, state) {
		let classVal = '' + (props.active?'active':'') + ' ' + (props.class?props.class:'');
		return (
			h('a', {
        href: props.href || '#',
				class: classVal,
				onclick: props.onClick,
				id: props.id,
        name: props.name
      },props.children)
		)
	}
}

export class Button extends Component {
	componentDidMount() {

	}

	render(props, state) {
		let classVal = '' + (props.active?'active':'') + ' ' + (props.class?props.class:'');
		return (
			h('button', {
        type: props.type || 'button',
				class: classVal,
				onclick: props.onClick,
				id: props.id,
        name: props.name
      },props.value,props.children)
		)
	}
}


export class ModalWindow extends Component {
	render(props, state) {
		let classValue = 'modal ' + (props.active?'active':'') + ' ' +  (props.class?props.class:'');
		return (
			h('div', {id: props.id, class: classValue, onClick: props.onClose},
				h('div', {class: 'modal-content', onClick: (e) => {e.stopPropagation();}},
					h('div', {class: 'modal-header'},
						h('h1',null,props.title),
						h('div',{class: 'modal-close'},
							h('a',{onClick: props.onClose},
								h(Icon,{icon: 'x-square'})
							)
					  ),
					),
					h('div', {class: 'modal-body'},props.children),
					h('div', {class: 'modal-footer'},props.footer)
				)
			)
		)
	}
}

export class InputField extends Component {
	render(props,state) {
		return (
			h('div',{class: 'field'},
				h('label',{for: props.id},props.label),
				h('input',{type: props.type, value: props.value, id: props.id, name: props.name, onChange: props.onChange}),
			)
		)
	}
}

export class TextInput extends Component {
	render(props,state) {
		return (
			h(InputField,{...props},props.children)
		)
	}
}


export class Form extends Component {
	render(props,state) {
		return (
			h('form',{onSubmit: props.onSubmit},props.children)
		)
	}
}

export class TabGroup extends Component {
	render(props,state) {
		return (
			h('div',{class:'tab-group'},props.children)
		)
	}
}

export class Tab extends Component {
	render(props,state) {
		return (
			h('div',{class:'tab'},props.children)
		)
	}
}
