import { h, Component, render } from 'preact';

class SVGPreviewRenderer extends Component {
	componentDidMount() {

	}
	render(props, state) {
		console.log('div',props.previewOutput)
		if (props.previewOutput) {
			// let renderPages = props.previewOutput.map( function(previewPage) {
			// 	return h('div',null, previewPage);
			// });

			return h('div',{class: 'svg-page', dangerouslySetInnerHTML : { __html: props.previewOutput}}, );
		}


	}
}

class PDFPreviewRenderer extends Component {
	componentDidMount() {

	}
	render(props, state) {
		return (
			h('iframe', {src: props.previewOutput})
		)
	}
}

export class PreviewRenderer extends Component {
	componentDidMount() {

	}
	render(props, state) {
		if (props.previewFormat == 'PDF') {
			return (
				h(PDFPreviewRenderer, {previewOutput: props.previewOutput})
			);
		} else {
			return (
				h(SVGPreviewRenderer, {previewOutput: props.previewOutput})
			);
		}
	}
}
