class RenderManager {
  static PageSizeDefaults = {
    A3: {
      x: 297,
      y: 420
    },
    A4: {
      x: 210,
      y: 297
    }
  }

  static RenderMode = Object.freeze({
    SVG: 'SVG',
    PDF: 'PDF'
  });

  static Events = [
    'renderStart',
    'renderOutput',
    'renderLog',
    'renderError',
    'renderFinish'
  ]

  constructor(opts) {
    //set defaults;
    this.pageSizes = RenderManager.PageSizeDefaults;


    this.pdfRenderOptions = {
      paged: true,
      pageSize: this.pageSizes.A4,
      styleSet: {
        base: {
          strokeWidth: '1',
          strokeColor: '#000'
        },
        center: {
          strokeWidth: '1',
          dashed: [10,20],
          strokeColor: '#0F0'
        },
        hint: {
          strokeWidth: '1',
          dashed: [5,5],
          strokeColor: '#00F'
        },
        stitch: {
          strokeWidth: '1',
          dashed: [2,10],
          strokeColor: '#F000'
        },
        patternJoinLine: {
          strokeWidth: '1',
          dashed: [2,10],
          strokeColor: '#b2c'
        },
      },
      pageMargins: 10,
      units: 'mm',
      layout: 'portrait',
    }

    this.svgRenderOptions = {
      paged: false,
      pageSize: this.pageSizes.A4,
      styleSet: {
          stitch: {
            stroke: 'red',
            strokeWidth: '1',
            cssStyle: 'stroke-dasharray: 5, 5'
          },
          center: {
            stroke: 'green',
            strokeWidth: '1',
            cssStyle: 'stroke-dasharray: 5, 5, 12, 5'
          },
          hint: {
            stroke: 'blue',
            strokeWidth: '1',
            cssStyle: 'stroke-dasharray: 2, 8'
          },
        }
    }

    this.splitterOptions = {
      enable: false
    }

    this.outputOptions = {
      defaultRenderMode: RenderManager.RenderMode.SVG,
    }

    this.eventHandlers = {}

    this._drawCode = '';
    this._userParams = '';
    this.renderOutput = null;
    this.renderErrors = null;
    this.renderInProgress = false;

    //TODO: Initialize the worker renderer
    this.renderWorker = opts.renderWorker;

    if (this.renderWorker) {
      this.renderWorker.onmessage = this.renderOutputReceived.bind(this);
      this.renderWorker.onerror = this.renderErrorReceived.bind(this);
    }


    //apply opts
    if (opts.pageSizes) {
      Object.apply(this.pageSizes,opts.pageSizes);
    }

    if (opts.pdfRenderOptions) {
      Object.apply(this.pdfRenderOptions,opts.pdfRenderOptions);
    }

    if (opts.svgRenderOptions) {
      Object.apply(this.svgRenderOptions,opts.svgRenderOptions);
    }

    if (opts.splitterOptions) {
      Object.apply(this.splitterOptions,opts.splitterOptions);
    }

    if (opts.outputOptions) {
      Object.apply(this.outputOptions,opts.outputOptions);
    }

    console.log(this);
  }

  on(eventName,handler) {
    if (RenderManager.Events.includes(eventName)) {
      this.eventHandlers[eventName] = handler;
    }
  }

  emit(eventName,data) {
    if (this.eventHandlers[eventName]) {
      this.eventHandlers[eventName](data);
    }
  }

  renderOutputReceived(outputMessage) {

    if (outputMessage.data.messageType == 'renderOutput') {
      //Pick apart the response and extract the actual output
      if (outputMessage.data) {
        let renderResponseData = outputMessage.data;

        this.emit('renderOutput',renderResponseData);
      }

      this.renderInProgress = false;
      this.emit('renderFinish');
    } else if (outputMessage.data.messageType == 'log'){
      this.emit('renderLog',data);
    }

    //Otherwise just ignore
  }

  renderErrorReceived(outputError){
    this.emit('renderError',outputError);
    this.renderInProgress = false;
    this.emit('renderFinish');
  }

  requestRender(renderMode,returnAction) {
    if (!renderMode) {
      renderMode = this.outputOptions.defaultRenderMode;
    }
    let thisRenderOptions = {renderMode: renderMode};
    if (renderMode == RenderManager.RenderMode.PDF) {
      thisRenderOptions = Object.assign(thisRenderOptions,this.pdfRenderOptions);
    } else {
      thisRenderOptions = Object.assign(thisRenderOptions,this.svgRenderOptions);
    }


    console.log('Render requested',renderMode,returnAction,thisRenderOptions);

    if (!this.renderInProgress) {
      this.emit('renderStart');
      this.renderInProgress = true;
      this.renderWorker.postMessage({
        action: 'render',
        drawCode: this._drawCode,
        userParams: this._userParams,
        splitterOptions: this.splitterOptions,
        renderOptions: thisRenderOptions,
        returnAction: returnAction
      })
    } else {
      throw 'Render already in progress'
    }

  }

  renderOptionsToJSON() {
    let resultRenderOptions = {
      pdfRenderOptions: this.pdfRenderOptions,
      svgRenderOptions: this.svgRenderOptions,
      splitterOptions: this.splitterOptions,
      pageSizes: this.pageSizes,
      outputOptions: this.outputOptions
    }

    return JSON.stringify(resultRenderOptions,undefined,2);
  }

  loadRenderOptionsFromJSON(inputRenderOptions) {
    let inputObj = JSON.parse(inputRenderOptions);

    Object.assign(this.pdfRenderOptions,inputObj.pdfRenderOptions);
    Object.assign(this.svgRenderOptions,inputObj.svgRenderOptions);
    Object.assign(this.splitterOptions,inputObj.splitterOptions);
    Object.assign(this.pageSizes,inputObj.pageSizes);
    Object.assign(this.outputOptions,inputObj.outputOptions);
  }

  set drawCode(val) {
    this._drawCode = val;
  }

  get drawCode() {
    return this._drawCode;
  }

  set userParams(val) {
    this._userParams = val;
  }

  get userParams() {
    return this._userParams
  }
}


module.exports = RenderManager
