import { h, Component, render } from 'preact';
import { ModalWindow, Button } from '../utilcontrols/utilcontrols.js';

class CreditLine extends Component {
	render(props, state) {
		return (
      h('li',null,
				h('span',null,props.title),
				h('a',{href: props.link, target: '_blank'},props.linkName)
			)
    )
	}
}

class CreditsList extends Component {
	render(props, state) {
		return (
      h('ul',{class: 'credits-list'},
				props.children
			)
    )
	}
}

